$main-color: rgb(250, 209, 209);
$sub-color: rgb(168, 114, 114);
$sub2-color: #fddfdf;
$h2-size: 2em;
$shadow-color: rgb(168, 104, 104);
$common-size: 16px;
$width: 100%;

@mixin column($number) {
  width: $width / $number;
  height: 100%;
}

