* ================================= */

@mixin flex($direction, $align, $justify) {
  display: flex;
  flex-direction: $direction;
  align-items: $align;
  justify-content: $justify;
}

@mixin position($position, $top, $left) {
  position: $position;
  top: $top;
  left: $left;
}
