.btn {
  width: 200px;
  height: 50px;
  line-height: 50px;
  font-size: 0.9em;
  font-weight: 600;
  color: #fff;
  text-align: center;
  background-color: #000;
  cursor: pointer;
}

/* body */
.dark {
  background-color: #000;
}

.dark header {
  border-bottom: 1px solid #444;
  background-color: #000;
}

.dark .logo {
  background: url('https://heimish902.github.io/converse-clone-coding/logo_w.png') no-repeat center/cover;
}

.dark nav a {
  font-weight: 400;
  color: #fff;
}
.dark nav .left li:last-child a {
  color: #fff;
}
.dark nav .right button {
  background-color: transparent;
}
